@use "../global/variables.scss";
@use "../global/mixins.scss";

.main-header {
  &--nojs .main-header__logo-wrapper {
    height: 0;
  }

  &--nojs .main-header__nav {
    position: relative;

    &--closed {
      display: block;
    }
  }

  &--nojs .main-header__toggle {
    display: none;
  }

  @media (min-width: variables.$tablet-width) {
    &--nojs .main-header__nav {
      position: fixed;
    }
  }
}

.main-header__logo-wrapper {
  height: 56px;
  background: variables.$white;

  @media (min-width: variables.$tablet-width) {
    height: 0;
  }
}

.main-header__nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 56px;
  z-index: 5;

  &--closed {
    display: none;
  }

  &--opened {
    display: block;
  }

  @media (min-width: variables.$tablet-width) {
    position: fixed;
    display: block;
  }
}

.main-header__logo-link {
  position: absolute;
  top: 0;
  right: 50%;
  transform: translateX(50%);
  z-index: 4;

  @media (min-width: variables.$tablet-width) {
    position: fixed;
    z-index: 6;
  }
}

.main-header__logo-image {
  @include mixins.img;
}

.main-header__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 99;
  width: 56px;
  height: 56px;
  // border: none;
  background: variables.$blue-01;

  @media (min-width: variables.$tablet-width) {
    position: fixed;
    display: none;
  }
}
